@charset "UTF-8";
:root {
  --primary-color: #5352ed;
  --primary-hover: rgb(37.0732984293, 35.8062827225, 232.1937172775);
  --warning-color: #ff7f50;
  --warning-hover: rgb(255, 89.6971428571, 29);
  --success-color: #2ed573;
  --success-hover: rgb(34.8047808765, 173.1952191235, 91.984063745);
  --danger-color: #ff4757;
  --danger-hover: rgb(255, 20, 40.4347826087);
  --dark-color: #000000;
  --dark-hover: hsl(0, 0%, -10%);
  --light-color: #a4b0be;
  --light-hover: rgb(134.25, 150.1730769231, 168.75);
  --info-color: #1e90ff;
  --info-hover: rgb(0, 118.56, 234);
}

body {
  font-size: 14px;
  font-family: "Poppins", "Changa", sans-serif;
  font-weight: 500;
  background-color: whitesmoke;
}

a {
  color: inherit;
  text-decoration: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

span {
  display: inline-block;
}

button {
  background-color: transparent;
  border: none;
}

.button {
  display: inline-flex;
  align-items: center;
  border-radius: 4px;
  text-transform: uppercase;
  padding: 12px 20px;
  font-weight: 500;
  transition: 0.3s;
}

.button-sm {
  padding: 10px 16px;
}

.button-md {
  padding: 14px 24px;
}

.button-lg {
  padding: 18px 32px;
}

.button-primary {
  background-color: #5352ed;
  color: white;
}
.button-primary:hover {
  color: white;
  background-color: rgb(19.0497382199, 18.0471204188, 173.4528795812);
}

.button-warning {
  background-color: #ff7f50;
  color: white;
}
.button-warning:hover {
  color: white;
  background-color: rgb(207.5, 55.7285714286, 0);
}

.button-success {
  background-color: #2ed573;
  color: white;
}
.button-success:hover {
  color: white;
  background-color: rgb(22.0039840637, 109.4960159363, 58.1533864542);
}

.button-danger {
  background-color: #ff4757;
  color: white;
}
.button-danger:hover {
  color: white;
  background-color: rgb(198.5, 0, 17.2608695652);
}

.button-dark {
  background-color: #000000;
  color: white;
}
.button-dark:hover {
  color: white;
  background-color: hsl(0, 0%, -25%);
}

.button-light {
  background-color: #a4b0be;
  color: white;
}
.button-light:hover {
  color: white;
  background-color: rgb(94.375, 111.7980769231, 132.125);
}

.button-info {
  background-color: #1e90ff;
  color: white;
}
.button-info:hover {
  color: white;
  background-color: rgb(0, 79.8, 157.5);
}

.tooltip-inner {
  background-color: var(--dark-color);
  color: white;
  text-transform: capitalize;
}

.bs-tooltip-top .tooltip-arrow::before {
  border-top-color: var(--dark-color);
}

.bs-tooltip-bottom .tooltip-arrow::before {
  border-bottom-color: var(--dark-color);
}

.bs-tooltip-start .tooltip-arrow::before {
  border-left-color: var(--dark-color);
}

.bs-tooltip-end .tooltip-arrow::before {
  border-right-color: var(--dark-color);
}

.bs-tooltip-auto[data-popper-placement^=bottom] .tooltip-arrow::before,
.bs-tooltip-bottom .tooltip-arrow::before {
  border-bottom-color: var(--dark-color);
}

.offcanvas-header {
  box-shadow: 0 0 17px rgba(0, 0, 0, 0.1);
}
.offcanvas-title {
  font-size: 16px;
  text-transform: capitalize;
  font-weight: 700;
}
.offcanvas-title svg {
  margin-right: 12px;
  color: var(--primary-color);
}

.offcanvas-cart .offcanvas-footer {
  padding: 24px;
  color: #94A3B8;
  font-weight: 400;
}
.offcanvas-cart .offcanvas-footer p {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
}
.offcanvas-cart .offcanvas-footer p.total {
  color: #000;
  border-top: 1px solid #ddd;
  padding-top: 12px;
  margin-top: 16px;
  font-size: 16px;
  font-weight: 600;
}
.offcanvas-cart .cart-item {
  position: relative;
  display: flex;
  padding: 24px 16px;
  border: 1px dashed transparent;
  border-bottom: 1px solid #E2E8F0;
}
.offcanvas-cart .cart-item:hover {
  border-color: var(--dark-color);
}
.offcanvas-cart .cart-item:nth-child(odd) {
  background-color: #F8FAFC;
}
.offcanvas-cart .cart-item .item-image {
  flex-shrink: 0;
}
.offcanvas-cart .cart-item .item-image img {
  width: 80px;
  height: 60px;
  border-radius: 4px;
  object-fit: cover;
}
.offcanvas-cart .cart-item .item-info {
  flex-grow: 1;
  padding: 0 16px;
}
.offcanvas-cart .cart-item .item-info .title {
  font-size: 13px;
  text-transform: capitalize;
  font-weight: 600;
  line-height: 22px;
}
.offcanvas-cart .cart-item .price-amount {
  display: flex;
  align-items: center;
  margin-top: 4px;
}
.offcanvas-cart .cart-item .price-amount .price {
  font-size: 18px;
  font-weight: 700;
  margin-right: 4px;
}
@media (max-width: 992px) {
  .offcanvas-cart .cart-item .price-amount {
    display: block;
  }
  .offcanvas-cart .cart-item .price-amount .price {
    margin-bottom: 4px;
  }
}
.offcanvas-cart .cart-item .quantity-input {
  border: 1px solid #e5e7eb;
  border-radius: 50px;
  display: flex;
  margin-left: auto;
  height: 32px;
  background-color: white;
}
.offcanvas-cart .cart-item .quantity-input:hover {
  border-color: var(--primary-color);
}
.offcanvas-cart .cart-item .quantity-input input {
  width: 44px;
  border: none;
  text-align: center;
  outline: none;
  background-color: transparent;
}
.offcanvas-cart .cart-item .quantity-input button {
  width: 32px;
}
.offcanvas-cart .cart-item .item-remove-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background-color: #000;
  width: 18px;
  height: 18px;
  padding: 0;
  border-radius: 4px;
  display: none;
  display: grid;
  place-content: center;
}
.offcanvas-cart .cart-item .item-remove-btn:hover {
  background-color: var(--danger-color);
}
.offcanvas-cart .cart-item .item-remove-btn svg {
  color: #fff;
  width: 15px;
  height: 15px;
}
.offcanvas-cart .cart-item:hover .item-remove-btn {
  display: inline-grid;
}

.side-canvas-info h6 {
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  margin-bottom: 16px;
}
.side-canvas-info .socials svg {
  color: var(--dark-color);
}
.side-canvas-info .contact-info svg {
  color: var(--primary-color);
}
.side-canvas-info .subscribe form .form-control {
  font-size: 13px;
  line-height: 32px;
  border-radius: 4px;
  margin-bottom: 12px;
}

.form-control {
  border-radius: 0;
  font-size: 14px;
}
.form-control:focus {
  outline: none;
  border-color: var(--primary-color);
  box-shadow: none;
}

.dropdown-menu {
  border-radius: 4px;
  border: none;
  box-shadow: 0 0 17px rgba(0, 0, 0, 0.1);
}
.dropdown-menu .dropdown-menu {
  left: 100%;
  top: 0;
}

.dropdown-item {
  font-size: 14px;
  padding: 8px 16px;
  text-transform: capitalize;
  transition: 0.3s ease;
  color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
}
.dropdown-item.active, .dropdown-item.active:hover, .dropdown-item:active {
  color: #fff;
  background-color: #CDDC39;
}
.dropdown-item:hover {
  color: #000;
  background-color: #ededed;
}

.dropdown-item.dropdown-toggle::after {
  transform: rotate(-90deg);
  border-width: 5px;
  opacity: 0.7;
}

.navbar-nav .dropdown-item {
  display: flex;
  justify-content: space-between;
}

@media (max-width: 1366px) {
  .container {
    max-width: 1320px;
  }
}
@media (max-width: 992px) {
  .container {
    max-width: 960px;
  }
}
@media (max-width: 768px) {
  .container {
    max-width: 100%;
    padding-left: 15px;
    padding-right: 15px;
  }
}
.app-header {
  position: relative;
}

.app-header::before {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #f2f1e2, #dcecdd);
  z-index: -1;
}

.header {
  padding: 18px 0;
}
.header .navbar-toggler {
  width: 44px;
}

.header-logo a {
  font-weight: 700;
  font-size: 24px;
  letter-spacing: 0.5px;
}
.header-logo svg {
  width: 40px;
  height: 32px;
  color: var(--primary-color);
}

.header-search-area {
  display: flex;
  align-items: center;
  border: 1px solid #e5e7eb;
  padding: 6px 8px;
  background-color: #fff;
  border-radius: 4px;
}

.search-category-list {
  width: 180px;
}
.search-category-list svg {
  margin-right: 12px;
  width: 22px;
}

.dropdown-item.primary svg {
  color: #5352ed;
}

.dropdown-item.warning svg {
  color: #ff7f50;
}

.dropdown-item.success svg {
  color: #2ed573;
}

.dropdown-item.danger svg {
  color: #ff4757;
}

.dropdown-item.dark svg {
  color: #000000;
}

.dropdown-item.light svg {
  color: #a4b0be;
}

.dropdown-item.info svg {
  color: #1e90ff;
}

.header-social-links {
  align-self: center;
}
.header-social-links .nav {
  justify-content: end;
}
.header-social-links a {
  padding: 0 8px;
}
.header-social-links svg {
  width: 20px;
  color: var(--dark-color, #000);
}
@media (max-width: 1200px) {
  .header-social-links {
    display: none;
  }
}

.auth img {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  object-fit: cover;
}

.auth-info {
  text-align: right;
  padding-right: 16px;
  font-size: 12px;
}

button.auth {
  display: flex;
  align-items: center;
}

.auth-info h6 {
  font-size: 14px;
  margin-bottom: 3px;
}

.header-right button {
  padding: 0 10px;
  position: relative;
}

.header-right .shopping-cart span {
  background-color: #E91E63;
  position: absolute;
  width: 14px;
  height: 14px;
  border-radius: 50px;
  font-size: 10px;
  color: #fff;
  right: 7px;
  top: 6px;
}

@media (max-width: 992px) {
  .header-inner-wrap {
    border-radius: 5px;
    padding: 12px 8px;
    background-color: white;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
  }
}
@media screen and (min-width: 992px) {
  .navbar .offcanvas-navbar-inner {
    background-color: #fff !important;
    padding: 8px 12px;
  }
  .navbar .dropdown-menu {
    display: block;
    transition: 0.3s;
    transform: translateY(10px);
    visibility: hidden;
    opacity: 0;
    padding: 1rem;
  }
  .navbar .dropdown:hover > .dropdown-menu {
    cursor: pointer;
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
  }
  .navbar .nav-link {
    font-weight: 600;
    text-transform: uppercase;
    pointer-events: none;
    padding: 8px 12px !important;
    position: relative;
  }
  .navbar .nav-link::before {
    content: "";
    position: absolute;
    height: 14px;
    width: 1px;
    background: linear-gradient(45deg, #f2ea71, #c1ecc3);
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }
  .navbar .nav-item:first-of-type .nav-link::before {
    content: none;
  }
  .navbar span.count {
    background-color: #00BCD4;
    position: absolute;
    width: 18px;
    height: 18px;
    border-radius: 50px;
    font-size: 13px;
    color: white;
    right: 10px;
    top: 11px;
  }
  .navbar .shopping-cart {
    position: relative;
  }
  .navbar .shopping-cart span {
    background-color: #E91E63;
    color: white;
    position: absolute;
    width: 16px;
    border-radius: 50px;
    height: 16px;
    font-size: 11px;
    left: 32px;
    top: 10px;
  }
}
@media (max-width: 1024px) {
  .navbar-nav .phone,
  .navbar-nav .mail {
    display: none;
  }
}
@media screen and (max-width: 991px) {
  .offcanvas {
    overflow: hidden;
    width: 300px !important;
  }
  .offcanvas-header {
    box-shadow: none;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .navbar .navbar-nav .nav-link {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    padding: 16px 24px;
    color: #000;
    font-weight: 600;
  }
  .navbar .navbar-nav .nav-item {
    width: 100%;
  }
  .navbar .offcanvas-body {
    padding: 0;
  }
  .navbar .navbar-nav .nav-link::after {
    transform: rotate(-90deg);
  }
  .navbar .dropdown-menu {
    top: 0;
    left: 0;
    opacity: 1;
    padding: 0;
    width: 100%;
    height: 100%;
    display: block;
    box-shadow: none;
    position: absolute;
    visibility: visible;
    background-color: #fff;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
  }
  .navbar .dropdown .dropdown-menu {
    z-index: 2;
  }
  .navbar .dropdown-menu.show {
    transform: translateX(0);
    margin: 0;
  }
  .navbar .navbar-nav .dropdown {
    position: static;
  }
  .navbar .dropdown-menu .dropdown-item {
    padding: 16px 24px 16px 42px;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    font-weight: 600;
  }
  .navbar .navbar-nav .dropdown-menu-heading {
    display: flex;
    align-items: center;
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    justify-content: space-between;
    height: 45px;
  }
  .navbar .navbar-nav .dropdown-menu-heading .btn-back {
    margin-right: 16px;
    border-right: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    padding: 0 16px;
    height: 100%;
    display: inline-grid;
    place-content: center;
  }
  .navbar .navbar-nav .dropdown-menu-heading svg {
    width: 19px;
  }
  .navbar .navbar-nav .dropdown-menu-heading .btn-close-canvas {
    padding: 16px;
    border-left: 1px solid rgba(0, 0, 0, 0.1);
    cursor: pointer;
    height: 100%;
    display: inline-grid;
    place-content: center;
  }
  .dropdown-item.active::after {
    content: "✔";
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
  .dropdown-item.active {
    position: relative;
    background-color: rgba(0, 0, 0, 0.03);
  }
  .dropdown-item.active,
  .dropdown-item.active:hover,
  .dropdown-item:active {
    color: var(--warning-color);
    background-color: transparent;
  }
  .navbar .dropdown .dropdown-menu.visible {
    transform: translateX(0);
  }
  .navbar .navbar-nav .nav-link:hover,
  .dropdown-item:hover {
    color: var(--warning-color);
    background-color: transparent;
  }
  .dropdown-toggle::after {
    display: inline-block;
    margin-left: 0.255em;
    vertical-align: 0.255em;
    content: "";
    border-top: 4px solid;
    border-right: 4px solid transparent;
    border-bottom: 0;
    border-left: 4px solid transparent;
  }
}
.hero-section {
  padding: 84px 0;
}

.hero-text h2 {
  font-size: 64px;
  font-weight: 700;
  text-transform: capitalize;
}

.hero-text p {
  font-size: 16px;
  margin: 24px 0;
  text-transform: capitalize;
  line-height: 32px;
}

.hero-text h2 span:first-of-type {
  background-color: var(--warning-color);
  color: white;
  padding: 0 8px;
}

.hero-text h2 span#changing-text {
  font-weight: 300;
  display: block;
  height: 76px;
}

.hero-image {
  position: relative;
  text-align: right;
  z-index: 1;
}

.hero-image img {
  height: 550px;
  right: 8%;
  bottom: 0;
  top: -50px;
}

.hero-image::before {
  position: absolute;
  content: "";
  width: 500px;
  height: 500px;
  background: linear-gradient(to bottom, #fff, transparent);
  border-radius: 50%;
  right: 0;
  z-index: -1;
  top: 0;
}

.typed-cursor {
  display: none;
}

.floating-img {
  position: absolute;
  transition: transform 0.1s linear;
  pointer-events: none;
}

img.floating-img.book {
  left: 54%;
  bottom: 20px;
}

img.floating-img.red-plus {
  top: 40%;
  left: 45%;
}

img.floating-img.crown {
  top: 50%;
  right: 140px;
  z-index: 1;
}

img.floating-img.dots {
  top: 180px;
  right: 30px;
}

@media (max-width: 992px) {
  .app-header {
    position: relative;
    height: 100vh;
  }
  .hero-text h2 {
    font-size: 32px;
    font-weight: 500;
    text-transform: capitalize;
  }
  .hero-section {
    padding: 24px 0;
  }
  .hero-text h2 span#changing-text {
    font-weight: 300;
    display: inline-block;
    height: auto;
  }
  .hero-image::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 320px;
  }
  .hero-image img {
    height: 340px;
    top: -50px;
  }
  .hero-text .button {
    border-radius: 50px;
    padding: 9px 22px;
  }
  .hero-text .button span {
    display: inline-grid;
    place-content: center;
    background-color: white;
    border-radius: 50px;
    margin-right: 8px;
    width: 36px;
    height: 36px;
    transform: translateX(-100%);
    opacity: 0;
    visibility: hidden;
    width: 0;
    transition: 0.3s;
  }
  .hero-text .button span svg {
    color: var(--primary-color);
  }
  .hero-text .button:hover span {
    width: 36px;
    transform: translateX(0);
    opacity: 1;
    visibility: visible;
  }
  .hero-text .button:hover {
    background-color: var(--primary-color);
  }
  img.floating-img.dots {
    top: 43%;
    right: 30px;
  }
  img.floating-img.crown {
    top: 70%;
    right: 16%;
    z-index: 1;
  }
  img.floating-img.book {
    left: 14%;
    bottom: 11%;
    z-index: 9;
  }
}